<template>
  <div>
    <img class="icons arrow" id="arrow1" :src="image">
    <p class="description">
      This web have two pages which are using Contentful. Enjoy!
    </p>
  </div>
</template>

<script>

export default {
  name: 'NewsPage',
  data () {
    return {
      image: ''
    }
  },
  created () {
    this.$contentful
      .getEntries()
      .then((res) => {
        this.image = res.items[1].fields.imageNews[0].fields.file.url
        console.log(res.items[1].fields.imageNews[0].fields.file.url)
      })
      .catch((error) => {
        console.log(error)
      })
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}

.description {
  margin-top: 5%;
  padding: 15px;
}
</style>



