<template>
  <div>
    <img src="../assets/develom_logo.png">
    <h1> {{title}} </h1>
    <div id="menu">
      <div id="left" @click="route('NewsPage')">
        <div id="left-cont" @mouseover="mouseOver('news')" @mouseout="mouseOut('news')">
          <p id="news">News</p>
          <div id="arrow-left">
            <img class="icons arrow" id="arrow1" src="../assets/icons/arrow-right.png">
          </div>
        </div>
      </div>
      <div id="right" @click="route('BlogPage')">
        <div id="right-cont" @mouseover="mouseOver('blog')" @mouseout="mouseOut('blog')">
          <p id="blog">Blog</p>
          <div id="arrow-right">
            <img class="icons arrow" id="arrow2" src="../assets/icons/arrow-right.png">
          </div>
        </div>
      </div>
    </div>
    <p class="description">
      This web have two pages which are using Contentful. Enjoy!
    </p>
  </div>
</template>

<script>
import { TweenLite, Sine } from 'gsap'
export default {
  name: 'MainPage',
  data () {
    return {
      title: "Hi! Develom's Here",
      arrows: String
    }
  },
  methods: {
    mouseOver: function (pg) {
      if (pg === 'news') {
        this.arrows = 'arrow1'
      } else if (pg === 'blog') {
        this.arrows = 'arrow2'
      }
      TweenLite.to('#' + pg, 0.5, { x: '-16' })
      TweenLite.to('#' + this.arrows, 0.6, { x: '27', ease: Sine.easeOut })
    },
    mouseOut: function (pg) {
      TweenLite.to('#' + pg, 0.5, { x: '0' })
      TweenLite.to('#' + this.arrows, 0.4, { x: '0', ease: Sine.easeOut })
    },
    route: function (data) {
      this.$router.push('/' + data)
      console.log(data)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}

#menu {
  margin-top: 50px;
  display: flex;
  justify-content: center;
  font-size: 25px;
  width: 100%;
  height: 80px;
}

#left {
  border: black 1px solid;
  width: 120px;
  margin-right: 15px;
  cursor: pointer;

  #left-cont {
    position: absolute;
    width: 120px;
    height: 79px;
    overflow: hidden;
  }

  p {
    position: absolute;
    left: 29px;
    background-color: #fff;
    border-radius: 10px;
    z-index: 2;
  }

  #arrow-left {
    position: relative;
    overflow: hidden;
    top: 27px;
    left: 80px;
    width: 40px;

    #arrow1 {
      position: relative;
      left: -35px;
    }
  }

}

#right {
  border: black 1px solid;
  width: 120px;
  margin-left: 15px;
  cursor: pointer;

  #right-cont {
    position: absolute;
    width: 120px;
    height: 79px;
    overflow: hidden;
  }

  p {
    position: absolute;
    left: 36px;
    background-color: #fff;
    border-radius: 10px;
    z-index: 2;
  }

  #arrow-right {
    position: relative;
    overflow: hidden;
    top: 27px;
    left: 74px;
    width: 40px;

    #arrow2 {
      position: relative;
      left: -35px;
    }
  }
}
.description {
  margin-top: 5%;
  padding: 15px;
}
</style>



