import Vue from 'vue'
import Router from 'vue-router'
import ContentfulVue from 'contentful-vue'
import MainPage from '@/components/MainPage'
import NewsPage from '@/components/pages/NewsPage'

Vue.use(Router)
Vue.use(ContentfulVue, {
  space: 'bijqeqjp96cx',
  accessToken: 'afdfda2847f56ef16432f7bc119b1cf9237d708627c793d00bd37a979a824d03'
})

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/myblog/',
      name: 'MainPage',
      component: MainPage
    },
    {
      path: '/NewsPage',
      name: 'NewsPage',
      component: NewsPage
    }
  ]
})



